html {
  font-family: Arial;
  font-size: 16px;
  background: aliceblue; }

.sidebar {
  position: absolute;
  width: 85%;
  height: 100vmin;
  background: #312450;
  font-size: 0.65em; }

.nav {
  height: 100vmin;
  position: absolute;
  margin: 0 15%;
  text-align: center;
  font-weight: bold; }

.nav ul {
  height: 100vmin;
  width: 100%;
  list-style: none; }
  .nav ul li {
    position: relative;
    margin: 8vmin 0; }
    .nav ul li a {
      line-height: 5vmin;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 0.4em;
      color: rgba(255, 255, 255, 0.35);
      display: block;
      -webkit-transition: all ease-out 300ms;
      transition: all ease-out 300ms; }
      .nav ul li a.active {
        color: white; }
      .nav ul li a:not(.active)::after {
        opacity: 0.2; }
      .nav ul li a:not(.active):hover {
        color: rgba(255, 255, 255, 0.75); }
      .nav ul li a::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 0.2em;
        background: black;
        left: 0;
        bottom: 0;
        background-image: -webkit-gradient(linear, left top, right top, from(#5e42a6), to(#b74e91));
        background-image: -webkit-linear-gradient(left, #5e42a6, #b74e91);
        background-image: linear-gradient(to right, #5e42a6, #b74e91); }

